@import "../variables.scss";
@import "../mixin.scss";

.home-page {
    width: 100vw;
    overflow: auto;
    outline: hidden;
    margin-top: $nav-bar-height;
    height: 100vh;

    // ORDER PART
    &__order-ways {
        height: calc(2 * (100vh - #{$nav-bar-height}) / 5);
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        // background-color: red;

        .option {
            width: calc(100% / 2.2);
            height: calc(100% / 1.5);
            box-shadow: rgba($text-color-primary, 0.12) 0px 30px 60px -12px inset,
                rgba(0, 0, 0, 0.2) 0px 18px 36px -18px inset;
            border-radius: 7px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            animation: fadeInUp 0.5s;
            position: relative;
            @include respond(tab-land) {
                width: calc(100% / 5);
            }

            .option-overlay {
                text-align: center;
                text-justify: center;
                p {
                    padding: 1rem;
                }
            }
            .option-information {
                width: 100%;
                display: flex;
                justify-content: space-evenly;
                display: none;
                @include center-vertically;

                & .link {
                    flex: 1;
                    background-color: $background-button;
                    border-radius: 30%;
                    box-shadow: 0px 2px 7px rgba($text-color-primary, 0.8);
                    margin: 0.5rem;
                    padding: 0.15rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    @include respond(tab-land) {
                        margin: 1.5rem;
                    }
                    & img {
                        width: 100%;
                    }
                }

                & .link.tel {
                    flex: none;
                    padding: 0.5rem;

                    @include respond(phone){
                        padding: 2rem;
                    }
                }
            }

            &:hover .option-information,
            &:focus .option-information {
                display: flex;
                transition: all 1s;
            }

            &:hover .option-overlay,
            &:focus .option-overlay {
                animation: fadeOut 1s;
                opacity: 0;
                transform: opacity 0.1s 1s;
            }
        }
    }

    // PRODUCTS PART
    &__products {
        overflow-y: hidden;
        h1 {
            margin-bottom: $margin-bottom-small;
        }
        margin-left: 2rem;
        margin-right: 2rem;
    }
}
